@import url(https://fonts.googleapis.com/css2?family=Commissioner&family=Cuprum&display=swap);
.navbar {
  justify-content: space-between;
  width: 100%;
  padding: 25px 0;
  background: #fff;
}

.nav-logo-container {
  margin-left: 55px;
  width: 50px;
}

.nav-links {
  font-size: 1.1rem;
  list-style: none;
  margin: 0 10px;
}

.link {
  text-decoration: none;
  margin: 0 20px;
  width: 150px;
}

.cart {
  width: 50px;
  margin-right: 40px;
}

.fa-shopping-cart {
  color: #adadad;
}

.fa-shopping-cart:hover {
  color: #000;
  transform: scale(1.2);
}

/* ============ Sidebar ============= */

.menu-icon-container {
  margin-right: 55px;
}

.menu-icon-container > .menu-icon:hover {
  cursor: pointer;
}

.nav-menu-toggle {
  display: block;
  position: absolute;
  right: 23px;
  top: 10px;
  color: #fff;
}

.nav-menu-toggle:hover {
  cursor: pointer;
}

.nav-menu {
  position: fixed;
  right: -100%;
  top: 0;
  width: 100%;
  height: 100vh;
  background: #596c5a;
  z-index: 100;
  transition: 0.5s;
}

.nav-menu.active {
  right: 0;
  transition: 0.5s;
}

.nav-menu-links {
  list-style: none;
  font-size: 2rem;
  margin-top: 4rem;
  width: 100%;
}

.nav-menu-link {
  text-decoration: none;
  border: 2px solid #fff;
  padding: 10px;
  margin: 25px;
}

.nav-menu-link:hover {
  background: rgba(255, 255, 255, 0.25);
}

.nav-menu-text {
  color: #fff;
  text-align: center;
}

/* =================================== */

.link-black {
  color: #000;
  border: 1px solid #d6d6d6;
}

.link-brown {
  background: #db9768;
  color: #fff;
  border: 1px solid #ca7d45;
}

.link-black:hover {
  background: #000;
  color: #fff;
}

.link-brown:hover {
  background: #596c5a;
  border: 1px solid #485849;
}

footer {
  width: 100%;
  color: white;
  background: #596c5a;
}

.footer-container {
  margin: 0 20px;
}

.footer-logo {
  box-sizing: unset;
  padding: 50px 20px;
  height: 75px;
}

.footer-content-container {
  margin-bottom: 20px;
}

.footer-content {
  flex: 1 1;
  padding: 25px;
}

.footer-content > a {
  color: #fff;
}

.footer-content-title {
  letter-spacing: 2px;
  padding-bottom: 20px;
  font-size: 1.25rem;
}

.footer-content-sub-title {
  padding-bottom: 10px;
  font-size: 0.9rem;
}

.footer-content-text {
  padding-bottom: 30px;
}

.border-top {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.border-right {
  border-right: 1px solid rgba(255, 255, 255, 0.1);
}

.copyright {
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.9rem;
  padding: 10px 0 30px;
}

.social-icon {
  width: 36px;
  height: 40px;
  margin-bottom: 20px;
}

.home-container {
  width: 100%;
  text-align: center;
}

.home-video-container {
  margin: 25px auto;
  height: 500px;
}

.home-video-container > video {
  width: 100%;
  height: 100%;
}

.home-text-container {
  box-sizing: unset;
  width: 100%;
  height: 150px;
  text-align: center;
  padding: 25px 0;
  color: #fff;
  background: #596c5a;
}

.home-text-med {
  font-size: 2rem;
  margin: 0 125px;
  font-weight: bold;
  line-height: 1.6;
  letter-spacing: 1px;
}

.home-text-sm {
  font-size: 1.45rem;
  margin: 0 40px;
  font-weight: bold;
  line-height: 1.6;
  letter-spacing: 1px;
}

.home-text-menu {
  font-size: 2.5rem;
  font-weight: bold;
  letter-spacing: 3px;
}

.gallery {
  margin: 100px;
}

.gallery-item {
  height: 100%;
  margin: 10px;
}

.gallery-item > img {
  width: 100%;
  height: 100%;
}

.menu-link {
  padding: 20px;
}

.about-content {
  width: 100%;
  text-align: center;
}

.about-omomo {
  height: 100vh;
}

.section-header-med {
  margin: 100px 0 50px;
  font-size: 2.75rem;
  letter-spacing: 0.2em;
}

.section-text-med {
  margin: 0 150px 150px;
  font-size: 1.75rem;
  font-weight: bold;
  line-height: 1.6;
}

.section-header-sm {
  margin: 75px 0 40px;
  font-size: 2.25rem;
  letter-spacing: 0.2em;
}

.section-text-sm {
  margin: 0 50px 100px;
  font-size: 1.1rem;
  font-weight: bold;
  line-height: 1.75;
}

.banner {
  margin-top: 25px;
  width: 100%;
}

.banner > img {
  width: 100%;
}

.blank {
  height: 100vh;
  background: #fff;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 1;
  background: rgba(0, 0, 0, 0.6);
}

.modal-content {
  position: relative;
  width: 550px;
  height: 80%;
  background: #fff;
}

.modal-actions {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 90px;
  width: 100%;
  border-top: 1px solid #c1c1c1;
  background: #fff;
}

.close {
  position: absolute;
  top: 0;
  right: 5px;
}

.close > .fa {
  color: #6d6d6d;
}

.close > .fa:hover {
  cursor: pointer;
  color: #3f3f3f;
}

.item-information {
  height: 100%;
}

.item-information > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.item-information > .load-image-failed {
  width: 100%;
  height: 100%;
  font-size: 1.5rem;
}

.quantity-form {
  width: 125px;
  margin: 15px;
  border: 1px solid #a1a1a1;
}

.quantity-form > .btn {
  width: 100%;
  font-size: 2rem;
  text-align: center;
  border: none;
  background: #fff;
  color: #c1c1c1;
}

.quantity-form > .btn:focus {
  outline: none;
}

.quantity-form > .btn:hover {
  cursor: pointer;
  color: #3f3f3f;
  background: #e4e4e4;
}

.quantity-form > .quantity-container {
  text-align: center;
  width: 50px;
}

.quantity-form > .quantity-container > input {
  width: 40px;
  height: 100%;
  border: none;
  font-family: arial;
  font-size: 1.25rem;
  text-align: center;
}

.quantity-form > .quantity-container > input:focus {
  outline: none;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.add-to-cart-form {
  width: 100%;
  margin: 15px 15px 15px 0px;
}

.add-to-cart-container {
  width: 100%;
  height: 100%;
}

.add-to-cart-container > .add-to-cart-btn {
  width: 100%;
  height: 100%;
  border: none;
  font-size: 1.5rem;
  color: #fff;
  background: #7a9c7c;
}

.add-to-cart-container > .add-to-cart-btn:hover {
  cursor: pointer;
  opacity: 0.9;
}

.add-to-cart-container > .add-to-cart-btn:focus {
  outline: none;
}

.menu-container {
  margin: 50px 55px;
}

.menu {
  width: 100%;
}

.menu-item {
  box-sizing: border-box;
  width: 225px;
  min-width: 110px;
}

.menu-item > img {
  width: 100%;
}

.menu-item > img:hover {
  cursor: pointer;
  opacity: 0.6;
}

.menu-item-description {
  font-size: 1.1rem;
  text-align: center;
}

.menu-item-name {
  padding: 5px;
}

.cart-container {
  margin-top: 25px;
  height: auto;
}

.cart-display {
  width: 100%;
  margin: 0 50px 50px;
  align-items: center;
}

.cart-summary {
  position: relative;
  margin-right: 50px;
  margin-bottom: 50px;
  width: 400px;
  height: 200px;
  border: 1px solid;
}

.cart-summary > h2 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  letter-spacing: 1px;
  padding: 10px;
  border-bottom: 1px solid;
  background: #cfcece;
}

.cart-summary > div {
  font-size: 1.25rem;
  padding: 0 10px;
  margin-bottom: 10px;
  justify-content: space-between;
}

.cart-summary > .checkout {
  position: absolute;
  bottom: 0;
  height: 40px;
  width: 100%;
  font-size: 1.15rem;
  letter-spacing: 2px;
  font-weight: bold;
  border: none;
  color: #000;
  background: #db9768;
}

.cart-summary > .checkout:hover {
  cursor: pointer;
  opacity: 0.8;
}

.cart-item {
  position: relative;
  margin-bottom: 10px;
  padding: 10px;
  width: 100%;
  max-width: 1000px;
  background: #88aa8b;
  border-radius: 5px;
}

.cart-item-image-container {
  width: 75px;
}

.cart-item-image-container > img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.cart-item-information {
  margin-left: 10px;
  padding: 5px 10px;
  width: 100%;
  border-radius: 5px;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.5);
}

.cart-item-name {
  font-size: 1.25rem;
  letter-spacing: 1px;
}

.cart-item-quantity-container {
  height: 23px;
}

.cart-item-quantity-container > input {
  height: 100%;
  width: 40px;
  font-size: 1.1rem;
  text-align: center;
  align-content: center;
}

.cart-item-quantity-container > input:focus {
  outline: none;
}

.cart-item-price {
  font-size: 1.15rem;
}

.cart-item-remove {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 30px;
  height: 30px;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  color: #628064;
  background: rgba(255, 255, 255, 0.55);
}

.cart-item-remove:hover {
  cursor: pointer;
  transform: scale(1.15);
  background: #fff;
}

.empty-cart-message {
  width: 100%;
  font-size: 2rem;
  font-family: sans-serif;
  text-align: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.flex {
  display: flex;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.center-items {
  justify-content: center;
  align-items: center;
}

.margin-right-25 {
  margin-right: 25px;
}

.margin-bot-25 {
  margin-bottom: 25px;
}

.margin-bot-50 {
  margin-bottom: 50px;
}

.margin-bot-100 {
  margin-bottom: 100px;
}

.font-brown {
  color: #db9768;
}

.cpm-text {
  font-family: Cuprum, sans-serif;
}

.comm-text {
  font-family: Commissioner, monospace;
}

